.orders {
    padding: 1.25rem 5rem;
    background-color: lightgray;
    
  }
  
  .orders__title {
    margin: 1.75rem 0;
  }
  .order {
    position: relative;
    padding: 2.5rem;
    margin: 1.25rem 0;
    border: 1px solid lightgray;
    background-color: white;
  }
  
  .order__id {
    position: absolute;
    top: 2.5rem;
    right: 1.25rem;
  }
  
  .order__total {
    font-weight: 500;
    text-align: right;
  }